import {
  Button,
  Dropdown,
  Input,
  Menu,
  Modal,
  Select,
  Space,
  Typography,
  notification,
  Form,
  Spin,
} from "antd";
import React, { useState } from "react";
import {
  CloseOutlined,
  LinkOutlined,
  Loading3QuartersOutlined,
  LoadingOutlined,
  LoginOutlined,
  MenuOutlined,
  PlayCircleOutlined,
  QuestionCircleOutlined,
  StarOutlined,
  ThunderboltOutlined,
} from "@ant-design/icons";
import "antd/dist/antd.less";
import "antd/dist/antd.dark.less";
import "./App.less";
import axios from "axios";
import ParticlesBg from "particles-bg";
import { Twirl as Hamburger, Twirl } from "hamburger-react";

const { Text, Link } = Typography;

// const HOST: string = "http://localhost:4000/"
const HOST: string = "/"

const itemStyle = {
  fontSize: "1.2em",
  padding: "15px 10px",
};

const menu = (
  <Menu style={{ width: 200 }}>
    <Menu.Item
      style={itemStyle}
      key="login"
      title="Login"
      icon={<LoginOutlined />}
    >
      <a target="_blank" rel="noopener noreferrer" href="/login">
        Login
      </a>
    </Menu.Item>
    <Menu.Item
      style={itemStyle}
      key="login"
      title="About us"
      icon={<StarOutlined />}
    >
      <a target="_blank" rel="noopener noreferrer" href="/about">
        About us
      </a>
    </Menu.Item>
    <Menu.Item
      style={itemStyle}
      key="login"
      title="Help"
      icon={<QuestionCircleOutlined />}
    >
      <a target="_blank" rel="noopener noreferrer" href="/help">
        Help
      </a>
    </Menu.Item>
  </Menu>
);

function App() {
  const [isCreateModal, setIsCreateModal] = useState<boolean>(false);
  const [isJoineModal, setIsJoinModal] = useState<boolean>(false);
  const [translation, setTranslation] = useState<string>("");
  const [translationType, setTranslationType] = useState<string>("WEB");
  const [translationLink, setTranslationLink] = useState<string>("");
  const [isCreateLoading, setIsCreateLoading] = useState<boolean>(false);
  const [isJoinLoading, setIsJoinLoading] = useState<boolean>(false);
  const [isRedirecting, setIsRedirecting] = useState<boolean>(false);
  const [isOpennedMenu, setIsOpennedMenu] = useState<boolean>(false);
  const [isActive, setIsActive] = useState<boolean>(false);

  const onBackStartClick = () => {
    form.resetFields();
    setIsCreateModal(false);
  };

  const onStartClick = () => {
    setIsCreateLoading(true);

    axios
      .post(HOST + "createtranslation", {
        name: translation,
        type: translationType,
      })
      .then((response) => {
        setIsRedirecting(true);
        window.location.replace(HOST  + response.data);
      })
      .catch((err) =>
        notification.error({
          message: <Text type="danger">Create translation error!</Text>,
          description:
            `Some error on server. Try to enter address again!\n` + err.message,
          placement: "bottomLeft",
        })
      )
      .finally(() => setIsCreateLoading(false));
  };

  const onBackJoinClick = () => {
    form2.resetFields();
    setIsJoinModal(false);
  };

  const onJoinClick = () => {
    setIsJoinLoading(true);

    const url = translationLink;

    axios
      .post(HOST + "jointranslation", {
        url,
      })
      .then(() => {
        setIsRedirecting(true);
        window.location.replace(HOST + url);
      })
      .catch((err) => {
        notification.error({
          message: <Text type="danger">There is no such translation!</Text>,
          description: `There is no broadcast at the addsess '${url}'. Try to enter address again!`,
          placement: "bottomLeft",
        });
      })
      .finally(() => setIsJoinLoading(false));
  };

  const [form] = Form.useForm();
  const [form2] = Form.useForm();

  const buttonStyle = {
    width: 270,
    height: 50,
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    fontSize: "1.1em",
    backgroundColor: "rgba(31, 31, 31, 0.5)",
  };
  return (
    <div className="App">
      {isRedirecting ? (
        <div
          style={{
            width: "100%",
            height: "100%",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Spin className="Spin" size="large" />
        </div>
      ) : (
        <>
          <Space
            style={{
              width: "100%",
              height: 50,
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              backgroundColor: "rgb(31,31,31)",
              boxShadow: " 1px 1px 20px 0px rgba(0, 0, 0, 0.7)",
            }}
            align="end"
          >
            <Link
              style={{
                fontSize: "1.6em",
                color: "#fff",
                fontWeight: 500,
                padding: "10px 20px",
              }}
            >
              <ThunderboltOutlined style={{ fontSize: "1.5em" }} />
              Live Coding
            </Link>
            <Dropdown
            arrow
              placement="bottomRight"
              overlay={menu}
              trigger={["click"]}
              onVisibleChange={(e) => {
                setIsOpennedMenu(e);
              }}
            >
              <div style={{ margin: "0px 20px" }}>
                <Twirl
                  toggle={setIsOpennedMenu}
                  toggled={isOpennedMenu}
                  rounded
                  distance="md"
                  size={35}
                />
              </div>
            </Dropdown>
          </Space>
          <Space
            style={{
              display: "flex",
              width: "100%",
              height: "100%",
              alignItems: "center",
              justifyContent: "center",
              fontSize: "1.3em",
            }}
          >
            <Space align="center" direction="vertical">
              <Button
                size="large"
                style={buttonStyle}
                onClick={() => setIsCreateModal(true)}
              >
                START TRANSLATION
                <PlayCircleOutlined
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    fontSize: "1.5em",
                  }}
                />
              </Button>

              <Text type="secondary">or</Text>

              <Button style={buttonStyle} onClick={() => setIsJoinModal(true)}>
                JOIN TRANSLATION
                <LinkOutlined
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    fontSize: "1.5em",
                  }}
                />
              </Button>
            </Space>
          </Space>
          <Modal
            title="Start new translation"
            visible={isCreateModal}
            onCancel={onBackStartClick}
            centered
            footer={[
              <Button
                key="back"
                onClick={onBackStartClick}
                disabled={isCreateLoading}
              >
                Close
              </Button>,
              <Button
                className="Button"
                key="start"
                type="primary"
                htmlType="submit"
                form="create_translation"
                disabled={isCreateLoading}
              >
                Start
              </Button>,
            ]}
          >
            {isCreateLoading ? (
              <div
                style={{
                  width: "100%",
                  height: "100%",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  fontSize: "8em",
                }}
              >
                <LoadingOutlined />
              </div>
            ) : (
              <Form
                onFinish={onStartClick}
                name="create_translation"
                form={form}
                labelCol={{ span: 12 }}
                wrapperCol={{ span: 16 }}
                labelAlign="left"
              >
                <Form.Item
                  name="Project Name"
                  label={
                    <Text style={{ fontSize: "1.2em" }}>
                      Type some project name
                    </Text>
                  }
                  rules={[{ required: true }]}
                >
                  <Input
                    value={translation}
                    onChange={(e) => setTranslation(e.target.value)}
                    allowClear
                  ></Input>
                </Form.Item>
                <Form.Item
                  name="Project Type"
                  label={
                    <Text style={{ fontSize: "1.2em" }}>
                      Choose project type
                    </Text>
                  }
                  rules={[{ required: true }]}
                >
                  <Select
                    placeholder="Select a type of project"
                    value={translationType}
                    onChange={(e) => setTranslationType(e)}
                    allowClear
                  >
                    <Select.Option value="WEB">
                      <Text>WEB</Text>
                    </Select.Option>
                    <Select.Option value="JAVA" disabled>
                      <Text>JAVA</Text>
                    </Select.Option>
                    <Select.Option value=".Net" disabled>
                      <Text>.Net</Text>
                    </Select.Option>
                    <Select.Option value="NodeJS" disabled>
                      <Text>Node JS</Text>
                    </Select.Option>
                  </Select>
                </Form.Item>
              </Form>
            )}
          </Modal>
          <Modal
            title="Join to translation"
            visible={isJoineModal}
            onOk={onJoinClick}
            onCancel={onBackJoinClick}
            centered
            footer={[
              <Button
                key="back"
                onClick={onBackJoinClick}
                disabled={isJoinLoading}
              >
                Close
              </Button>,
              <Button
                className="Button"
                key="start"
                type="primary"
                htmlType="submit"
                form="join_translation"
                disabled={isJoinLoading}
              >
                Join
              </Button>,
            ]}
          >
            {isJoinLoading ? (
              <div
                style={{
                  width: "100%",
                  height: "100%",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  fontSize: "7.35em",
                }}
              >
                <LoadingOutlined />
              </div>
            ) : (
              <Form
                onFinish={onJoinClick}
                name="join_translation"
                form={form2}
                labelCol={{ span: 9.5 }}
                wrapperCol={{ span: 20 }}
              >
                <Form.Item
                  name="Translation Link"
                  label={
                    <Text style={{ fontSize: "1.2em" }}>
                      Type translation`s link
                    </Text>
                  }
                  rules={[{ required: true }]}
                >
                  <Input
                    value={translationLink}
                    onChange={(e) => setTranslationLink(e.target.value)}
                    addonBefore="http://livecoding/"
                    allowClear
                    style={{ width: 470, marginTop: 15 }}
                  ></Input>
                </Form.Item>
              </Form>
            )}
          </Modal>
          <ParticlesBg color="#ffffff"  type="cobweb" bg={true} />
        </>
      )}
    </div>
  );
}

export default App;
